.body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}
.services {
  display: flex;
  justify-content: space-between;
  padding-right: 50px;
  padding-left: 50px;
  min-height: 18vh;
}

.line {
  border-right: 2px solid white;
}

.services .item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  color: white;
  font-weight: 400;
}

.chat_us {
  width: 100%;
  display: flex;
  justify-content: center;
}

.chat_us .btn {
  color: #fff;
  border: 1px solid #fff;
  width: 10%;
  padding: 10px;
  align-items: center;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  /* For Tablets & mobile Phones */
  .team_img_mob {
    /* position: absolute; */
    background-image: url('../../assets/image/bg_web_mob.PNG');
    min-height: 50vh;
    background-repeat: no-repeat;
  }

  .services .item {
    font-size: 1.2rem;
  }

  .chat_us .btn {
    width: 20%;
  }
}

@media only screen and (min-width: 768px) {
  /* For Desktops */

  .services {
    margin-top: 120px;
  }
}
